              </div>
            <!--   <div class="nameInput">
                <span class="tab span">名单总数量</span>
                <el-input v-model="lowerNum" placeholder="请输入名单数量"></el-input>
              </div>
              <div class="nameInput">
                <span class="tab span" style="width:120px">名单剩余数量</span>
                <el-input v-model="distributeNum" placeholder="请输入名单剩余数量"></el-input>
              </div> -->
