            <el-table-column  align="center" prop="batchId" label="批次号" width="120" show-overflow-tooltip></el-table-column>
            <el-table-column  align="center" prop="batchName" label="批次名称" width="100" show-overflow-tooltip></el-table-column>
            <el-table-column  align="center" prop="codeName" label="名单来源" show-overflow-tooltip></el-table-column>
            <el-table-column  align="center" prop="sonCodeName" label="子名单来源" show-overflow-tooltip></el-table-column>
