<script scpped>
import { getListIssued ,getCustomerList} from '@/api/info'
import fliterRouter from '@/utils/filterRouter'
export default {
  name: "questionBank",
  data() {
    return {
      batchId: "",//批次号信息
      batchName: "",//批次号名称
      codeName: "",//名单来源
      sonCodeName: "",//名单子来源
      lowerNum: "",//名单数量
      distributeNum: "",//名单剩余数量
      tsrCode:'',//当前坐席

      pageSize:10,//每页条数
      total:0,//总共条数
      currentPage4: 1,
      pageNum:1,//当前页数

      cursturTotal:0,//客户信息总条数
      cursturPageSize:10,//客户信息每页条数
      cursturCurrentPage: 1,
      cursturPageNum : 1,//客户信息当前页数

      tableData: [],//名单分配查询列表
      cursturTableData:[],//客户信息列表
      classType: "全部",
      bankType: "全部",
      dialogVisible: false, //题库名称点击弹框
      delateDigFlag: false, //删除列表提示弹框
      sureDelateDigFlag: false, //确定删除提示框
      questionNum: "一",
      multipleSelection: [], //选择框选中列表
      listIncloudFalg: false,//名单下发组件
      orgId:'',//客户信息
      currentCusBatchId:'',//当前客户id
      position:'',
      allCationList:false,
      showPagination: false,
    };
  },
  created(){
    let userStorage = JSON.parse(sessionStorage.getItem('userInfo')); 
    this.tsrCode = userStorage.tsrCode;
    this.orgId = userStorage.orgId;
    this.position = userStorage.position;
    fliterRouter[0].children[0].icon = 'T'
  },
  mounted(){
    //查询默认数据
    this.getListIssued();
  },
  methods: {
    getListIssued(changePageNum){
      let pageNum = changePageNum? changePageNum:this.pageNum;
      let data = {
          pageSize:this.pageSize,
          pageNum:pageNum,
          batchId: this.batchId,
          batchName: this.batchName,
          codeName: this.codeName,
          sonCodeName: this.sonCodeName,
        /*   lowerNum: this.lowerNum,
          distributeNum:this.distributeNum, */
          orgId: this.orgId,
          position: this.position
        }
        //console.log(data)
        getListIssued(data).then(res =>{
          if(res.code = '000'){
            let data = res 
            this.tableData = data && data.dataList;
            if(this.tableData.length == 0){
              this.allCationList = true;
            }else{
              this.allCationList = false;
              this.showPagination = true;
            }
            this.total = data && parseInt(data.dataCount) ;
          }
          
        }).catch(err =>{
       this.$message.error('操作失败');
     })
    },
    // 操作
    handleEdit(index, row) {
      this.delateDigFlag = true;
      this.getCustomerd(row)
    },
    //客户列表详情
    getCustomerd(row){
      this.currentCusBatchId = row &&  row.batchId ? row.batchId : this.currentCusBatchId
      let param = {
        batchId :  this.currentCusBatchId,
        pageSize :this.cursturPageSize, 
        pageNum : this.cursturPageNum,
        orgId: this.orgId,
      }

      getCustomerList(param).then(res=>{
         if(res.code = '000'){
            let data = res 
            this.cursturTableData = data && data.dataList;
            this.cursturTotal = data && parseInt(data.dataCount) ;
          }
      }).catch(err =>{
       this.$message.error('操作失败');
     })
    },
    //跳转名单下发页面
    listIncloud(){
        let mapBatchId =[];
        if(this.multipleSelection.length > 0){
          for( let i = 0; i < this.multipleSelection.length; i++){
            let obj = {};
            obj.batchId = this.multipleSelection[i].batchId;
            mapBatchId.push(obj)
          }
          //动态修改路由icon
          fliterRouter[0].children[0].icon = 'F'
          this.$router.push({ path: '/listIncloud',query:{mapBatchId:mapBatchId }})
       
        }else{
         this.$message('请勾选名单列表');
        }
       
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },

    questionDig(index, rows) {
     // console.log(index, rows);
      this.dialogVisible = true;
      this.questionNum = index + 1;
    },

    delateDig() {
      if (this.multipleSelection.length == 0) {
        this.delateDigFlag = true;
      } else {
        this.sureDelateDigFlag = true;
      }
    },
    //首页
    handleSizeChange(val) {
    /*   console.log(`每页 ${val} 条`); */
    },
    handleCurrentChange(val) {
     // console.log(val)
      this.pageNum = val
      this.getListIssued(this.pageNum)
    },
    //客户详情页面
    cusHandleSizeChange(val){
      // console.log(`每页 ${val} 条`);
    },
    cusHandleCurrentChange(val){
      this.cursturPageNum = val
      this.getCustomerd();
    }
  } //methods
}; 
</script>

